import React, { Component } from 'react';
import Progress from '../components/progress';
import 'animate.css';
import './loading.scss';

class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            progress: 0,
        }
    }

    componentDidMount() {
        if (!window.__PAGE_DATA__.USER_ID) {
            return this.props.history.replace("/");
        }
        let count = 0;
        let sum = 0;
        if (window.__PAGE_DATA__.IMAGES && window.__PAGE_DATA__.IMAGES.length) {
            sum = window.__PAGE_DATA__.IMAGES.length;
            const loadImage = function() {
                count++;
            };
            for (let item of window.__PAGE_DATA__.IMAGES) {
                const image = new Image();
                image.onload = loadImage;
                image.onerror = loadImage;
                image.src = item;
            }

        }
        const loadProgress = setInterval(() => {
            const progress = this.state.progress + 20;
            if (progress === 100) {
                if (count === sum) {
                    this.setState({ progress });
                }
            } else if (progress < 100) {
                this.setState({ progress });
            } else {
                clearInterval(loadProgress);
                this.props.history.replace("/home");
            }
        }, 500);
    }

    render() {
        return (
            <div className="Loading">
                <i className="title-icon title-icon-lg"></i>
                <span className="Loading-title animated infinite jello">
                    Loding...
                </span>
                <Progress progress={this.state.progress}></Progress>
            </div>
        );
    }

}

export default Login;
