/* eslint-disable no-undef */
/* eslint-disable react/jsx-no-undef */
import React, { Component } from 'react';
import Loading from '../components/loading';
import axios from 'axios';
import './pharmacy.scss';

class Pharmacy extends Component {

    constructor(props) {
        super(props);
        this.state = {
            bgTypes1: [
                [
                    [
                        "b1-begin1",
                        "b1-ques1",
                    ],
                    [
                        "b1-begin2",
                        "b1-ques2",
                    ],
                    [
                        "b1-begin2",
                        "b1-ques2",
                    ],
                ],
                [
                    [
                        "b2-begin1",
                        "b2-ques1",
                    ],
                    [
                        "b2-begin2",
                        "b2-ques2",
                    ],
                    [
                        "b2-begin2",
                        "b2-ques2",
                    ],
                ],
                [
                    [
                        "b3-begin1",
                        "b3-ques1",
                    ],
                    [
                        "b3-begin2",
                        "b3-ques2",
                    ],
                    [
                        "b3-begin2",
                        "b3-ques2",
                    ],
                ],
                [
                    [
                        "b4-begin1",
                        "b4-ques1",
                    ],
                    [
                        "b4-begin2",
                        "b4-ques2",
                    ],
                    [
                        "b4-begin2",
                        "b4-ques2",
                    ],
                ],
            ],
            bgTypes2: [
                [
                    "p-choice-1",
                    "p-answer-1",
                ],
                [
                    "p-choice-2",
                    "p-answer-2",
                ],
                [
                    "p-choice-3",
                    "p-answer-3",
                ],
            ],
            userType: window.__PAGE_DATA__.DOCTOR_TYPE || 0,
            pharmacyType: window.__PAGE_DATA__.STUDY_TYPE || 0,
            pageType: 0,
            checked: {},
            answers: [
                true,
                false,
                false,
                false,
            ],
            problem: '',
            score: 5,
            token: '',
            error: 1,
            isLoading: true,
            problemId: 0,
        }
    }

    componentDidMount() {
        if (!window.__PAGE_DATA__.USER_ID) {
            return this.props.history.replace("/");
        }
        axios.post('http://yuyi.jpxia.cn/home/index/dati', {
            userId: window.__PAGE_DATA__.USER_ID,
        }).then(res => {
            const { data: { response = {} }} = res;
            this.setState({
                pharmacyType: response.studyType || 0,
                problem: response.problem || '',
                option: response.option || [],
                answers: response.answer || [],
                score: response.score - 0,
                token: response.token,
                error: response.error,
                isLoading: false,
                problemId: response.problemId,
            });
        }).catch(err => {
            console.log(err);
        });
    }

    back = () => {
        switch(this.state.pageType) {
            case 0:
                this.props.history.replace("/home");
                break;
            case 1:
                this.setState({ pageType: 0 });
                break;
            case 2:
                this.setState({ pageType: 1 });
                break;
            case 3:
                this.props.history.replace("/home");
                break;
            default:
                break;
        }
    }

    onInquiry = () => {
        if (this.state.error === 0) {
            this.setState({ pageType: 1 });
        } else {
            alert("无诊可问！");
        }
    }

    onCheck = () => {
        this.setState({ pageType: 2 })
    }

    onChecked = (e) => {
        const { checked } = this.state;
        const id = e.target.id;
        if (checked[id]) {
            checked[id] = false;
        } else {
            checked[id] = true;
        }

        this.setState({ checked });
    }

    onSubmit = () => {
        const { answers } = this.state;
        const checked = [this.state.checked.A, 
            this.state.checked.B, 
            this.state.checked.C, 
            this.state.checked.D];
        let res = true;
        for (let i = 0; i < 4; i++) {
            if (!answers[i] !== !checked[i]) {
                res = false;
                break;
            }
        }
        if (res) {
            this.setState({ isLoading: true });
            axios.post('http://yuyi.jpxia.cn/home/index/jiafen', {
                userId: window.__PAGE_DATA__.USER_ID,
                token: this.state.token,
                type: 1,
                score: this.state.score,
                problemId: this.state.problemId,
            }).then(res => {
                const { data: { response = {} }} = res;
                if (response.error === 0) {
                    window.__PAGE_DATA__.SCORE += this.state.score;
                    if (window.__PAGE_DATA__.SCORE < window.__PAGE_DATA__.LEVEL_SCORE[0]) {
                        window.__PAGE_DATA__.DOCTOR_TYPE = 2;
                    } else if (window.__PAGE_DATA__.SCORE < window.__PAGE_DATA__.LEVEL_SCORE[1]) {
                        window.__PAGE_DATA__.DOCTOR_TYPE = 1;
                    } else {
                        window.__PAGE_DATA__.DOCTOR_TYPE = 0;
                    }
                    this.props.history.replace({
                        pathname: '/success', 
                        state: { 
                            score: this.state.score,
                            isShowTitle: true,
                        }
                    });
                } else {
                    alert('出错了！');
                    this.setState({ isLoading: false });
                }
            }).catch(err => {
                console.log(err);
            });
        } else {
            this.setState({ pageType: 3 })
        }
    }

    onFinish = () => {
        this.props.history.replace("/home");
    }

    render() {
        const { userType, pageType, checked, pharmacyType } = this.state;
        let bg = "";
        if (pageType < 2) {
            bg = this.state.bgTypes1[pharmacyType][userType][pageType];
        } else {
            bg = this.state.bgTypes2[userType][pageType - 2];
        }
        const optionID = ['A', 'B', 'C', 'D'];
        const buttonA = checked.A ? 'button-4' : 'button-1';
        const buttonB = checked.B ? 'button-4' : 'button-1';
        const buttonC = checked.C ? 'button-4' : 'button-1';
        const buttonD = checked.D ? 'button-4' : 'button-1';

        return (
            <div className={"Pharmacy " + bg}>
                <nav className="nav-bar">
                    <i className="back-icon" onClick={this.back}></i>
                    <div>
                        <i className="title-icon title-icon-sm"></i>
                        <i className="title-s-icon" style={{ marginBottom: '-20px' }}></i>
                    </div>
                </nav>

                <Choose>
                    <When condition={this.state.pageType === 0}>
                        <button className="button-md button-2" onClick={this.onInquiry}>问诊</button>
                    </When>
                    <When condition={this.state.pageType === 1}>
                        <div className="text-wrap-2">
                            <p>{this.state.problem}</p>
                            <ul className="choose-list">
                                <For each="item" index="index" of={this.state.option}>
                                    <li key={index}>{optionID[index] + '. ' + item}</li>
                                </For>
                            </ul>
                        </div>
                        <button className="button-md button-2" onClick={this.onCheck}>处方</button>
                    </When>
                    <When condition={this.state.pageType === 2}>

                        <div className="button-box" onClick={this.onChecked}>
                            <div className="button-line">
                                <button id="A" className={"button-md radio " + buttonA}>A</button>
                                <button id="B" className={"button-md radio " + buttonB}>B</button>
                            </div>
                            <div className="button-line">
                                <button id="C" className={"button-md radio " + buttonC}>C</button>
                                <button id="D" className={"button-md radio " + buttonD}>D</button>
                            </div>
                        </div>
                        
                        <button className="button-md button-2 choice-submit" onClick={this.onSubmit}>处方确认</button>
                    </When>
                    <When condition={this.state.pageType === 3}>
                    <div className="text-wrap-2">
                        <h4>正确答案</h4>
                        <p>{this.state.problem}</p>
                        <ul className="choose-list">
                            <For each="item" index="index" of={this.state.option}>
                                <li key={index}>
                                    <i className={this.state.answers[index].toString()}></i>
                                    {optionID[index] + '. ' + item}
                                </li>
                            </For>
                        </ul>
                    </div>
                    <button className="button-md button-2" onClick={this.onFinish}>确定</button>
                    </When>
                </Choose>

                <If condition={this.state.isLoading}>
                    <Loading></Loading>
                </If>
            </div>
        );
    }

}

export default Pharmacy;
