import React, { Component } from 'react';
import './home.scss';

class Home extends Component {

    onToClassRoom = () => {
        this.props.history.replace("/classroom");
    }

    onToPharmacy = () => {
        this.props.history.replace("/pharmacy");
    }

    onToRanking = () => {
        this.props.history.replace("/ranking");
    }

    componentDidMount() {
        if (!window.__PAGE_DATA__.USER_ID) {
            this.props.history.replace("/");
        }
    }

    render() {
        return (
            <div className="Home">
                <i className="title-icon title-icon-lg"></i>
                <button className="button-1 button-lg" onClick={this.onToClassRoom}>药方学堂</button>
                <button className="button-1 button-lg" onClick={this.onToPharmacy}>悬壶济世</button>
                <button className="button-1 button-lg" onClick={this.onToRanking}>等级排行</button>
            </div>
        );
    }

}

export default Home;
