/* eslint-disable no-undef */
/* eslint-disable react/jsx-no-undef */
import React, { Component } from 'react';
import Loading from '../components/loading';
import axios from 'axios';
import './ranking.scss';

class Ranking extends Component {

    constructor(props) {
        super(props);
        this.state = {
            bgClass: [
                [
                    "t1-bg-1",
                    "t1-bg-2",
                    "t1-bg-3"
                ],
                [
                    "t2-bg-1",
                    "t2-bg-2",
                    "t2-bg-3"
                ],
                [
                    "t3-bg-1",
                    "t3-bg-2",
                    "t3-bg-3"
                ],
            ],
            userType: window.__PAGE_DATA__.DOCTOR_TYPE || 0,
            pageType: 0,
            ranking: [],
            score: 0,
            level: 123,
            isLoading: true,
        };
    }

    componentDidMount() {
        if (!window.__PAGE_DATA__.USER_ID) {
            return this.props.history.replace("/");
        }
        axios.post('http://yuyi.jpxia.cn/home/index/paiming', {
            userId: window.__PAGE_DATA__.USER_ID,
        }).then(res => {
            const { data: { response = {} }} = res;
            this.setState({
                ranking: response.ranking,
                isLoading: false,
            });
        }).catch(err => {
            console.log(err);
        });
        axios.post('http://yuyi.jpxia.cn/home/index/jifen', {
            userId: window.__PAGE_DATA__.USER_ID,
        }).then(res => {
            const { data: { response = {} }} = res;
            window.__PAGE_DATA__.SCORE = response.score;
            if (response.score < window.__PAGE_DATA__.LEVEL_SCORE[0]) {
                window.__PAGE_DATA__.DOCTOR_TYPE = 2;
            } else if (response.score < window.__PAGE_DATA__.LEVEL_SCORE[1]) {
                window.__PAGE_DATA__.DOCTOR_TYPE = 1;
            } else {
                window.__PAGE_DATA__.DOCTOR_TYPE = 0;
            }
            this.setState({
                score: response.score,
                level: response.level,
                userType: window.__PAGE_DATA__.DOCTOR_TYPE,
            });
        }).catch(err => {
            console.log(err);
        });
    }

    back = () => {
        if (this.state.pageType === 1) {
            this.setState({ pageType: 0 });
        } else if (this.state.pageType === 2) {
            this.setState({ pageType: 0 });
        } else {
            this.props.history.replace("/home");
        }
    }

    onIntro = () => {
        this.setState({ pageType: 1 });
    }

    onMyLevel = () => {
        this.setState({ pageType: 2 });
    }

    render() {
        const bgClass = this.state.bgClass[this.state.userType][this.state.pageType];
        const numberZh = ['一', '二', '三', '四', '五', '六', '七', '八', '九', '十'];
        return (
            <div className={"Ranking " + bgClass}>
                <nav className="nav-bar">
                    <i className="back-icon" onClick={this.back}></i>
                    <div>
                        <i className="title-icon title-icon-sm"></i>
                        <i className="title-s-icon" style={{ marginBottom: '-20px' }}></i>
                    </div>
                </nav>
                
                <Choose>
                    <When condition={this.state.pageType === 0}>
                        <div className="text-wrap-2">
                            <ul>
                                <For each="item" index="index" of={this.state.ranking}>
                                    <li key={index}>
                                    第{numberZh[index]}名：
                                    <span>{item.name}</span>
                                    {item.level + '(' + item.integral + '分)'}
                                    </li>
                                </For>
                            </ul>
                        </div>

                        <div className="button-bar">
                            <button className="button-2 button-md" onClick={this.onIntro}>积分说明</button>
                            <button className="button-2 button-md" onClick={this.onMyLevel}>我的积分</button>
                        </div>
                    </When>
                    <When condition={this.state.pageType === 1}>

                    </When>
                    <When condition={this.state.pageType === 2}>
                        <div className="level-wrap">
                            <h2>我的积分：{this.state.score}</h2>
                            <h2>我的排行：{this.state.level}</h2>
                        </div>
                    </When>
                </Choose>

                <If condition={this.state.isLoading}>
                    <Loading></Loading>
                </If>

            </div>
        );
    }

}

export default Ranking;
