/* eslint-disable react/jsx-no-undef */
import React, { PureComponent } from 'react';
import './success.scss';
import 'animate.css';

class Success extends PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            successTypes: [
                'Success-1',
                'Success-2',
                'Success-3',
            ],
            userType: window.__PAGE_DATA__.DOCTOR_TYPE || 0,
        }
    }

    componentDidMount() {
        if (!window.__PAGE_DATA__.USER_ID && this.props.location.state) {
            return this.props.history.replace("/");
        }
    }

    onToHome = () => {
        this.props.history.replace("/home");
    }

    render() {
        let score = 5;
        let isShowTitle = false;
        console.log(this.props);
        if (this.props.location.state) {
            score = this.props.location.state.score || 5;
            isShowTitle = this.props.location.state.isShowTitle || false;
        }
        return  (
            <div className={"Success " + this.state.successTypes[this.state.userType]}>
                <nav className="nav-bar">
                    <i></i>
                    <div>
                        <i className="title-icon title-icon-sm"></i>
                        <i className="title-s-icon" style={{ marginBottom: '-20px' }}></i>
                    </div>
                </nav>

                <div className="score">
                    <div className="animated fadeInUp">
                        <div className="score-title">{ '+ ' + score}</div>
                        <If condition={isShowTitle}>
                            <div className="score-s-title">
                                炼药成功
                            </div>
                        </If>
                    </div>
                </div>
                <div className="handel">
                    <button onClick={this.onToHome} className="button-2 button-md animated bounceInUp delay-1s">完成</button>
                </div>

            </div>
        )
    }

}

export default Success;
