/* eslint-disable no-undef */
/* eslint-disable react/jsx-no-undef */
import React, { Component } from 'react';
import Loading from '../components/loading';
import Hammer from 'hammerjs';
import axios from 'axios';
import 'animate.css';
import './classRoom.scss';

class ClassRoom extends Component {

    constructor(props) {
        super(props);
        this.$max = null;
        this.state = {
            isMax: false,
            type: 0,
            beginStudy: false,
            bgClass: [
                "classRoom-1",
                "classRoom-2"
            ],
            pageNum: 2,
            timeCount: 30,
            courseList: [],
            course: "",
            isTick: false,
            courseListleft: 0,
            courseListleftStick: 0,
            listStartX: 0,
            maxClientX: 0,
            maxClientY: 0,
            maxTopStick: 200,
            maxLeftStick: 0,
            maxTop: 200,
            maxLeft: 0,
            maxWidth: "100%",
            isFinished: false,
            score: 15,
            courseToken: null,
            isLoading: false,
            isLoadingMask: true,
        };
    }

    componentDidMount() {
        if (!window.__PAGE_DATA__.USER_ID) {
            return this.props.history.replace("/");
        }
        const { changeMax } = this;
        const hammer = new Hammer(this.$max);
        hammer.get('pinch').set({ enable: true });
        hammer.on('pinch', function(e) {
            const newWidth = e.scale > 1 ? e.scale : 1;
            changeMax(newWidth * 100 + '%');
        });
        axios.post('http://yuyi.jpxia.cn/home/index/shangke', {
            userId: window.__PAGE_DATA__.USER_ID,
            pageNum: 1,
        }).then(res => {
            const { data: { response = {} }} = res;
            if (response.coursesUrl) {
                const course = response.coursesUrl[0];
                const courseList = response.coursesUrl;
                this.setState({ 
                    courseList,
                    course,
                    isFinished: !!response.isFinished,
                    score: response.score - 0,
                    courseToken: response.courseToken,
                    timeCount: response.isFinished ? -1 : 30,
                    isLoadingMask: false,
                });
            }
        }).catch(err => {
            console.log(err);
            alert(err);
        });
    }

    changeMax = width => {
        this.setState({ maxWidth: width })
    }

    toStudy = () => {
        this.setState({ type: 1 });
        this.beginTimer();
    }

    back = () => {
        if (this.state.type === 0) {
            this.props.history.replace("/home");
        } else {
            this.setState({ type: 0 });
        }
    }

    switchCourse = e => {
        if (e.target.id) {
            const { courseList } = this.state;
            this.setState({ course: courseList[e.target.id], isTick: false});
        }
    }

    beginTimer = () => {
        this.setState({ course: this.state.courseList[0], isTick: true });
        if (this.state.timeCount >= 0) {
            const timer = setInterval(() => {
                const { timeCount, type } = this.state;
                if (timeCount < 0 || !this.state.isTick || type === 0) {
                    clearInterval(timer);
                } else {
                    this.setState({ timeCount: timeCount - 1 });
                }
            }, 1000);
        }
    }

    onHideMax = () => {
        this.setState({ isMax: false, maxLeftStick: 0, maxTopStick: 200, maxLeft: 0, maxTop: 200, maxWidth: "100%" });
    }

    onShowMax = () => {
        this.setState({ isMax: true });
    }

    onclassOver = () => {
        if (this.state.timeCount < 0) {
            if (!this.state.isFinished) {
                this.setState({ isLoadingMask: true });
                axios.post('http://yuyi.jpxia.cn/home/index/jiafen', {
                    userId: window.__PAGE_DATA__.USER_ID,
                    token: this.state.courseToken,
                    type: 0,
                    score: this.state.score,
                }).then(res => {
                    const { data: { response = {} }} = res;
                    if (response.error === 0) {
                        window.__PAGE_DATA__.SCORE += this.state.score;
                        if (window.__PAGE_DATA__.SCORE < window.__PAGE_DATA__.LEVEL_SCORE[0]) {
                            window.__PAGE_DATA__.DOCTOR_TYPE = 2;
                        } else if (window.__PAGE_DATA__.SCORE < window.__PAGE_DATA__.LEVEL_SCORE[1]) {
                            window.__PAGE_DATA__.DOCTOR_TYPE = 1;
                        } else {
                            window.__PAGE_DATA__.DOCTOR_TYPE = 0;
                        }
                        this.props.history.replace({
                            pathname: '/success', 
                            state: { score: this.state.score }
                        });
                    } else {
                        alert('出错了！');
                        this.setState({ isLoadingMask: false });
                    }
                }).catch(err => {
                    console.log(err);
                    alert(err);
                });
            } else {
                alert("你已经学习过本课程了！");
            }
        }
    }

    onMoveStart = e => {
        this.setState({ listStartX: e.touches[0].clientX });
    }

    onMoveCourseList = e => { 
        let isLoading = false;
        if (e.touches.length) {
            let maxLeft = ((5 - this.state.courseList.length) * 25);
            maxLeft = maxLeft < 0 ? maxLeft : 0;
            let newLeft = (e.touches[0].clientX - this.state.listStartX) / window.innerWidth * 100 
                + this.state.courseListleft;
            newLeft = newLeft < 0 ? newLeft : 0;
            newLeft = newLeft > maxLeft ? newLeft : maxLeft;
            if (this.state.courseListleftStick === maxLeft && e.touches[0].clientX - this.state.listStartX < 0) {
                newLeft = newLeft - 25;
                isLoading = true;
            }
            this.setState({ courseListleft: newLeft, isLoading });
        }
    }

    onMoveCourseListEnd = e => {
        const { courseListleft, courseListleftStick, courseList, pageNum, isLoading } = this.state;
        const diff = courseListleft - courseListleftStick;
        let maxLeft = ((5 - this.state.courseList.length) * 25);
        maxLeft = maxLeft < 0 ? maxLeft : 0;
        
        if (Math.abs(diff) > 20 && !isLoading) {
            let newLeft = diff > 0 ? courseListleftStick + 100 : courseListleftStick - 100;
            newLeft = newLeft < maxLeft ? maxLeft : newLeft;
            newLeft = newLeft > 0 ? 0 : newLeft;
            this.setState({ courseListleft: newLeft, courseListleftStick: newLeft });
        } else if (Math.abs(diff) > 20 && isLoading) {
            this.setState({ courseListleftStick: courseListleft, courseListleft: courseListleftStick });
            axios.post('http://yuyi.jpxia.cn/home/index/shangke', {
                userId: window.__PAGE_DATA__.USER_ID,
                pageNum,
            }).then(res => {
                const { data: { response = {} }} = res;
                if (response.coursesUrl && response.coursesUrl.length) {
                    this.setState({ 
                        courseList: [...courseList, ...response.coursesUrl],
                        courseListleftStick: courseListleftStick,
                        pageNum: pageNum + 1,
                        isLoading: false,
                    });
                } else {
                    setTimeout(() => {
                        this.setState({ courseListleftStick: courseListleftStick, isLoading: false });
                    }, 3000);
                }
            }).catch(err => {
                console.log(err);
                this.setState({ courseListleftStick: courseListleftStick, isLoading: false });
            });
        } else {
            this.setState({ courseListleft: courseListleftStick, isLoading: false });
        }
    }

    onMoveMax = e => {
        let newTop = (e.touches[0].clientY - this.state.maxClientY) + this.state.maxTopStick;
        let newLeft = (e.touches[0].clientX - this.state.maxClientX) / 50 + this.state.maxLeft;
        
        if (newLeft > 0) {
            newLeft = 0;
        } else if (newLeft < window.innerWidth - this.$max.width) {
            newLeft = window.innerWidth - this.$max.width;
        }
        if (this.$max.height > window.innerHeight) {
            if (newTop > 0) {
                newTop = 0;
            } else if (newTop < window.innerHeight - this.$max.height) {
                newTop = window.innerHeight - this.$max.height;
            }
        } else {
            if (newTop < 0) {
                newTop = 0
            } else if (newTop > window.innerHeight - this.$max.height) {
                newTop = window.innerHeight - this.$max.height;
            }
        }
        this.setState({ maxTop: newTop, maxLeft: newLeft });
    }

    onMoveMaxStart = e => {
        this.setState({ maxClientX: e.touches[0].clientX, maxClientY: e.touches[0].clientY });
    }

    onMoveMaxEnd = e => {
        const { maxTop, maxLeft } = this.state;
        this.setState({ maxTopStick: maxTop, maxLeftStick: maxLeft });
    }

    render() {
        const courseList = this.state.courseList.slice(1);
        const maxDisplay = this.state.isMax ? "block" : "none";
        return (
            <div id="classRoom" className={"ClassRoom " + this.state.bgClass[this.state.type]}>
                <nav className="nav-bar">
                    <i className="back-icon" onClick={this.back}></i>
                    <div>
                        <i className="title-icon title-icon-sm"></i>
                        <i className="title-s-icon" style={{ marginBottom: '-20px' }}></i>
                    </div>
                </nav>

                <Choose>
                <When condition={this.state.type === 0}>
                    <button className="button-2 button-md" onClick={this.toStudy}>上课</button>
                </When>
                <When condition={this.state.type === 1}>
                    <div className="course">
                        <img src={this.state.course} alt="course" className="course-content"/>
                        <div className="course-content-mask" onClick={this.onShowMax}></div>
                    </div>
                    <ul className="course-history" onClick={this.switchCourse} onTouchStart={this.onMoveStart}
                        onTouchMove={this.onMoveCourseList} onTouchEnd={this.onMoveCourseListEnd}
                        style={{ left: this.state.courseListleftStick + '%' }}>
                        <For each="item" index="index" of={courseList}>
                            <li key={index}>
                                <img src={item} alt="course"/>
                                <div id={index + 1} className="course-mask"></div>
                            </li>
                        </For>
                        <If condition={this.state.isLoading}>
                        <li>
                            <i className="course-loading animated rotateOut infinite"></i>
                        </li>
                        </If>
                    </ul>
                    <Choose>
                    <When condition={this.state.isTick}>
                        <button onClick={this.onclassOver} 
                            className={ this.state.timeCount < 0 ? "button-2 button-md" : "button-3 button-md"}>
                            下课
                            <If condition={this.state.timeCount >= 0}>
                                <span className="time-count">{this.state.timeCount}</span>
                            </If>
                        </button>
                    </When>
                    <Otherwise>
                        <button className="button-2 button-md" onClick={this.beginTimer}>
                            返回
                            <If condition={this.state.timeCount >= 0}>
                                <span className="time-count">{this.state.timeCount}</span>
                            </If>
                        </button>
                    </Otherwise>
                    </Choose>
                </When>
                </Choose>

                <div className="max-wrap" onClick={this.onHideMax} style={{ display: maxDisplay }}
                    onTouchStart={this.onMoveMaxStart} onTouchMove={this.onMoveMax} onTouchEnd={this.onMoveMaxEnd}>
                    <img id="maxImage" src={this.state.course} alt="course" className="max"
                        style={{ top: this.state.maxTop + 'px', left: this.state.maxLeft + 'px', width: this.state.maxWidth }}
                        ref={el => {this.$max = el}}/>
                </div>

                <If condition={this.state.isLoadingMask}>
                    <Loading></Loading>
                </If>
            </div>
        );
    }

}

export default ClassRoom;
