/* eslint-disable react/jsx-no-undef */
import React, { Component } from 'react';
import Loading from '../components/loading';
import axios from 'axios';
import './login.scss';

class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoading: true,
            userName: "",
            password: "",
            resource: [
                'static/images/background-3.jpg',
                'static/images/classintro.jpg',
                'static/images/cources.jpg',
                'static/images/score1.jpg',
                'static/images/score2.jpg',
                'static/images/score3.jpg',
                'static/images/scoreInfo-1.jpg',
                'static/images/scoreInfo-2.jpg',
                'static/images/scoreInfo-3.jpg',
                'static/images/success-1.jpg',
                'static/images/success-2.jpg',
                'static/images/success-3.jpg',
            ],
            showButton: true,
        }
    }

    componentDidMount() {
        for (let i of this.state.resource) {
            new Image().src = i;
        }
        setTimeout(() => {
            this.setState({ isLoading: false })
        }, 1000);
    }

    submit = () => {
        const { userName, password } = this.state;
        if (!userName) {
            alert("用户名不可为空！");
            return;
        }
        if (!password) {
            alert("密码不可为空！");
            return;
        }
        axios.post('http://yuyi.jpxia.cn/home/index/login', {
            "userId": userName,
            "password": password,
        }).then(res => { 
            const { data: { response = {} }} = res;
            if (response.error === 0) {
                window.__PAGE_DATA__.USER_ID = userName;
                axios.post('http://yuyi.jpxia.cn/home/index/shangke', {
                    userId: userName,
                    pageNum: 1,
                }).then(res => {
                    const { data: { response = {} }} = res;
                    if (response.coursesUrl) {
                        window.__PAGE_DATA__.IMAGES.unshift(...response.coursesUrl);
                        this.props.history.replace("/loading");
                    }
                }).catch(err => {
                    console.log(err);
                });
                axios.post('http://yuyi.jpxia.cn/home/index/jifen', {
                    userId: userName,
                }).then(res => {
                    const { data: { response = {} }} = res;
                    window.__PAGE_DATA__.SCORE = response.score - 0;
                    if (response.score < window.__PAGE_DATA__.LEVEL_SCORE[0]) {
                        window.__PAGE_DATA__.DOCTOR_TYPE = 2;
                    } else if (response.score < window.__PAGE_DATA__.LEVEL_SCORE[1]) {
                        window.__PAGE_DATA__.DOCTOR_TYPE = 1;
                    } else {
                        window.__PAGE_DATA__.DOCTOR_TYPE = 0;
                    }
                }).catch(err => {
                    console.log(err);
                });
                axios.post('http://yuyi.jpxia.cn/home/index/dati', {
                    userId: window.__PAGE_DATA__.USER_ID,
                }).then(
                    res => {
                        const { data: { response = {} }} = res;
                        window.__PAGE_DATA__.STUDY_TYPE = response.studyType;
                }).catch(err => {
                    console.log(err);
                });
            } else if (response.error === 1) {
                alert("密码错误！");
            } else if (response.error === 2) {
                alert("用户不存在！");
            } else {
                alert("未知的错误！");
            }
        }).catch(err => {
            console.log(err);
        });
    }

    onInputUser = e => {
        this.setState({ userName: e.target.value, showButton: true });
    }

    onInputPass = e => {
        this.setState({ password: e.target.value, showButton: true });
    }

    onFocus = () => {
        this.setState({ showButton: false });
    }

    render() {
        return (
            <div className="Login">
                <i className="title-icon title-icon-lg"></i>
                <div className="input-line">
                    <label htmlFor="userId">账号：</label>
                    <input id="userId" type="" placeholder="用户名/邮箱" onBlur={this.onInputUser}
                    onFocus={this.onFocus}></input>
                </div>
                <div className="input-line">
                    <label htmlFor="password">密码：</label>
                    <input id="password" type="password" placeholder="密码" onBlur={this.onInputPass}
                    onFocus={this.onFocus}></input>
                </div>
                <If condition={this.state.showButton}>
                    <button className="button-1 button-md" onClick={this.submit}>确认</button>
                </If>
                <If condition={this.state.isLoading}>
                    <Loading></Loading>
                </If>
            </div>
        );
    }

}

export default Login;
